:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

body {
  font-family: 'Roboto', sans-serif;
  background: #fff;
  font-size: 16px;
  padding: 30px;
  background-image: url(https://99px.ru/sstorage/53/2011/09/tmb_20355_3202.jpg);
  background-size: cover;
}

img,
video {
  padding: 10px;
}

button {
  display: block;
  /* margin-left: auto;
  margin-right: auto; */
}

button[data-action='load-more'].is-hidden {
  display: none;
}

button[data-action='load-more'] > .spinner-grow.is-hidden {
  display: none;
}

.error {
  color: red;
}

.search-form {
    
  display: flex;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 30px;
}

.input {
    
  margin-right: 40px;
  border-color: #000000;
}

.load-more {
    border-radius: 50px;
  font-family: inherit;
  font-size: inherit;
}

.submitBtn {
  border-radius: 5px;
  background-color: inherit;
}

.photo-card img {
  height: 100%;
  width: 100%;
  height: 300px;
  object-fit: cover;
  cursor: pointer;
}

.photo-card {
  background-color: rgba(255, 255, 255, 0.808);
  padding: 5px;
  margin: 15px;
  width: calc((100% - 120px) / 4);
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

table {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}

.photo-card:hover {
  box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);
  transform: scale(1.03);
}

.photo-card:nth-child(4n) {
  margin-right: 0;
}

.stats {
  display: flex;
  justify-content: space-evenly;
}

.stats-item {
  text-align: center;
  margin-right: 5px;
  margin-left: 5px;
}

.material-icons {
  display: flex;
  justify-content: center;
}

button {
    border-color: #3498db;
  color: #fff;
  box-shadow: 0 0 40px 40px #3498db inset, 0 0 0 0 #3498db;
  -webkit-transition: all 150ms ease-in-out;
  transition: all 150ms ease-in-out;
  
}
button:hover {
    box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;

  animation: slidebg 5s linear infinite;
}


@keyframes slidebg {
  to {
    background-position: 20vw;
  }
}

.input {
    
  height: 30px;
  padding-left: 10px;
  margin-right: 15px;

  border: 2px solid rgb(57, 133, 184);
  border-radius: 5px;

  font-size: 10px;
  line-height: 1.25;
  font-family: 'Architects Daughter', cursive;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
  background-color: rgba(248, 249, 250, 0.808);
  cursor: pointer;
}
/* кнопка вверх */

.back_to_top {
  position: fixed;
  bottom: 50px;
  right: 40px;
  z-index: 9999;
  width: 45px;
  background-color: rgba(0, 0, 0, 0.6);
  height: 30px;
  text-align: center;
  line-height: 31px;
  background: initial;
  font-size: 400%;
  /* color: #444; */
  opacity: 0.7;
  cursor: pointer;
  border-radius: 2px;
  display: none;
}

.back_to_top:hover {
  opacity: 1;
  
}

.back_to_top-show {
  display: block;
}
.up {
  font-size: 20px;
  margin: 0 -18px 0 0;
}